.bar{
	background-color: white;
	width: 100%;
	height: 10px;
	position: relative;
	border-radius: 30px;
	z-index: 0;

	.red{
		width: 20%;
		background-color: red;
		height: 10px;
		margin-top: 10px;
		position: relative;
		z-index: 10;
	}
}

span.fa.fa-check-circle{
	color: #3DBE3D;
}
.profilepic{
	width: 100%;
	height: 200px;

	img{
		width: 200px;
		float: right;
	}
}

.header_container_bck{
	min-height: 70em;
}

#user .small{
	font-size: 0.7em;
	color: red;
}

.notification{
	width: 100%;
	background-color: rgba(0,0,0, 0.3);
	margin-top: 30px;
	font-size: 0.9em;
	padding: 10px;

	.fa-bell{
		font-size: 0.6em;
	}
}

#phone{
	padding: 0px;
	margin: 0px;
	background-color: white;
	color: green;
}
.input-group{	
	border: 0px solid transparent;
}
.input-group .input-group-text{
	color: white;
	background-color: #3DBE3D;
	border: 0px solid transparent;
}
